// --sb-loader-copy-text-color - Text color
// --sb-loader-text-size - Text size
// --sb-loader-text-weight - Text weight
// --sb-loader-text-line-height - Text line height

.sb-loader-text {
  color: var(--sb-loader-copy-text-color, var(--sbt-theme-color-70));
  font-size: var(--sb-loader-text-size, var(--sbt-font-size-35));
  font-weight: var(--sb-loader-text-weight, var(--sbt-font-weight-70));
  line-height: var(--sb-loader-text-line-height, var(--sbt-line-height-50));
  margin: var(--sbt-spacing-0);
  padding: var(--sbt-spacing-0);
  text-align: center;
}
