// --sb-expandable-menu-section__color-hover - controls the color of the background color on hover and icon color (if icon is used)
// --sb-expandable-menu-section__elevation-hover - controls the elevation on hover
// --sb-expandable-menu-section__left-border-color - controls the color of the left border

%active-style {
  --sb-icon__color: var(
    --sb-expandable-menu-section__color-hover,
    var(--sbt-theme-color-70)
  );

  background-color: var(
    --sb-expandable-menu-section__color-hover,
    var(--sbt-theme-color-10)
  );

  border-left: var(--sbt-spacing-10) solid
    var(
      --sb-expandable-menu-section__left-border-color,
      var(--sbt-theme-color-30)
    );
  color: var(
    --sb-expandable-menu-section__color-hover,
    var(--sbt-theme-color-70)
  );

  .sb-expandable-menu-section__expandable-section {
    --sb-expandable-section_collapsed-width: var(--sbt-spacing-70);
    padding-left: var(--sbt-spacing-10);
  }
}

.sb-expandable-menu-section {
  cursor: pointer;

  &:hover {
    @extend %active-style;
  }
}

.sb-expandable-menu-section__active {
  --sb-expandable-menu-section__left-border-color: var(--sbt-theme-color-70);
  @extend %active-style;
  border-left: var(--sbt-spacing-10) solid var(--sbt-theme-color-70);
}
