// --sb-expandable-section_collapsed-width - how wide the main section will be
// --sb-expandable-section__gap - space between the main and expanded section
// --sb-expandable-section__childen-font-weight - font weight of children

.sb-expandable-section {
  display: grid;
  grid-gap: var(--sb-expandable-section_gap, var(--sbt-spacing-20));
  grid-template-columns:
    var(--sb-expandable-section_collapsed-width, var(--sbt-spacing-70))
    1fr;
  min-width: var(
    --sb-expandable-section_collapsed-width,
    var(--sbt-spacing-70)
  );
}

.sb-expandable-section__expanded {
  width: 100%;
}

.sb-expandable-section__main {
  align-self: center;
  justify-self: center;
  min-width: var(
    --sb-expandable-section_collapsed-width,
    var(--sbt-spacing-70)
  );
}

.sb-expandable-section__children {
  align-self: center;
  display: inline-block;
  font-size: var(--sbt-font-size-25);
  font-weight: var(
    --sb-expandable-section__childen-font-weight,
    var(--sbt-font-weight-40)
  );
  line-height: var(--sbt-line-height-25);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
