.sb-container {
  --sb-container_margin: var(
      --sb-container_margin-top,
      var(--sb-container_spacing-none, var(--sbt-spacing-0))
    )
    var(
      --sb-container_margin-right,
      var(--sb-container_spacing-none, var(--sbt-spacing-0))
    )
    var(
      --sb-container_margin-bottom,
      var(--sb-container_spacing-none, var(--sbt-spacing-0))
    )
    var(
      --sb-container_margin-left,
      var(--sb-container_spacing-none, var(--sbt-spacing-0))
    );

  --sb-container_padding: var(
      --sb-container_padding-top,
      var(--sb-container_spacing-none, var(--sbt-spacing-0))
    )
    var(
      --sb-container_padding-right,
      var(--sb-container_spacing-none, var(--sbt-spacing-0))
    )
    var(
      --sb-container_padding-bottom,
      var(--sb-container_spacing-none, var(--sbt-spacing-0))
    )
    var(
      --sb-container_padding-left,
      var(--sb-container_spacing-none, var(--sbt-spacing-0))
    );

  background-color: var(--sb-container_fill);

  border-color: var(--sb-container_stroke-color);
  border-radius: var(--sb-container_corner-radius);
  border-style: var(--sb-container_stroke-style);
  border-width: var(--sb-container_stroke-width);

  box-shadow: var(--sb-container_elevation);

  margin: var(--sb-container_margin);

  padding: var(--sb-container_padding);
}

.sb-container_size-full {
  height: 100%;
}

.sb-container_state {
  align-items: center;
  display: flex;
  justify-content: center;
  text-align: center;
}

.sb-container_state-error {
  --sb-container_fill: var(--sbt-color-error-10);
}

.sb-container_state-loading {
  --sb-container_fill: var(--sbt-black-alpha-10);
}

.sb-container_state-empty {
  --sb-container_fill: var(--sbt-theme-tertiary-color-10);
}

// Fill variants (transparent, white, gray, theme, and prism fills)
.sb-container_fill-transparent {
  --sb-container_fill: transparent;
}

.sb-container_fill-white {
